import { ref, reactive, computed, watch } from "vue";
import { useStore } from "vuex";
export default {
  name: "wifi_content",
  props: {
    msg: String
  },

  setup() {
    const store = useStore();
    let RequestUrl = store.state.RequestUrl; //用计算属性取到vuex中的值

    const getShowTask = computed(() => {
      return store.state.RequestUrl;
    }); //监视vux中请求地址的变化

    watch(getShowTask, (newVal, oldVal) => {
      RequestUrl = newVal;
      console.log("RequestUrl变化了");
    });
    let SendingAddress = "";
    let InputValue = reactive({
      WIFI_Name: "",
      WIFI_Pwd: "",
      encryption: "WPA/WPA2"
    });
    const showPicker = ref(false);
    const columns = ["WPA", "WEP", "无加密"];

    const onConfirm = val => {
      InputValue.encryption = val;
      showPicker.value = false;
    }; //点击生成二维码事件


    function become(values) {
      SendingAddress = RequestUrl + `WIFI:S:${values.WIFI_Name};T:WPA;P:${values.WIFI_Pwd};`; // 触发vuex中的actions，用于异步修改state的信息

      store.dispatch("upSendingAddress", SendingAddress);
    }

    ;
    return {
      InputValue,
      showPicker,
      columns,
      onConfirm,
      become
    };
  }

};